import { GestionErreurs } from '../Pages/Composent/GestionErreurs';

export class RechercheJeu {
    constructor(client) {
        this.client = client; 
    }

    // Méthode pour récupérer les données
    async fetchData(query) {
        try {
            const jeux = await this.client.searchGame(query);
            return jeux;
        } catch (error) {
            new GestionErreurs({
                type: 'danger',
                titre: 'Erreur de récupération',
                message: 'Erreur lors de la récupération des données : ' + error.message
            });
            throw error;
        }
    }

    // Méthode pour gérer la recherche
    async handleSearch(query, setStateCallback) {
        console.log("Nouvelle recherche :", query);

        // Si l'input est vide, ne pas effectuer de requête
        if (!query.trim()) {
            new GestionErreurs({
                type: 'warning',
                titre: 'Recherche vide',
                message: 'La recherche est vide. Aucune requête effectuée.'
            });
            return;
        }

        try {
            // Effectue la requête API avec la recherche de l'utilisateur
            const jeux = await this.fetchData(query);
            console.log("Résultats de recherche :", jeux);

            // Met à jour l'état via le callback
            setStateCallback({ resultat: jeux, rechercheEnCours: query });
        } catch (error) {
            new GestionErreurs({
                type: 'danger',
                titre: 'Erreur de recherche',
                message: 'Erreur lors de la recherche : ' + error.message
            });
        }
    }

    // Méthode pour charger les données initiales
    async fetchInitialData(setStateCallback) {
        console.log("Chargement initial des données...");
        try {
            const jeux = await this.fetchData("gta");
            setStateCallback({ resultat: jeux });
        } catch (error) {
            new GestionErreurs({
                type: 'danger',
                titre: 'Erreur initiale',
                message: 'Erreur lors du chargement initial : ' + error.message
            });
        }
    }
}